
import { _decorator, Component, Node, find, instantiate, Prefab, resources, Vec3, warn, log } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Utils')
export class Utils extends Component {
    
    public static loadPrefab(path: string, parent: Node, pos: Vec3, isSetWorldPos: boolean = false): Node {
        let node: Node = null!

        resources.load(path, Prefab, (err: any, prefab: Prefab) => {
            if (err) {
                warn("err:", err)
                return
            }

            if (isSetWorldPos) {
                node = instantiate(prefab) as Node
                node.setParent(parent)
                node.setWorldPosition(pos)
                log("utils - node1", node)
            } else {
                node = instantiate(prefab) as Node
                node.setParent(parent)
                node.setPosition(pos)
                log("utils - node2", node)
            }

        })

        log("utils - node3", node)
        return node
        
    }


    
}
